.sectionGallery .options .option {
  position: relative;
  overflow: hidden;
  min-width: 60px;
  margin: 10px;
  background-size: auto 120%;
  background-position: center;
  cursor: pointer;
  transition: 0.5s cubic-bezier(0.05, 0.61, 0.41, 0.95);
}

.sectionGallery .options .option.active {
  flex-grow: 10000;
  -webkit-transform: scale(1);
          transform: scale(1);
  max-width: 600px;
  margin: 0px;
  border-radius: 40px;
  background-size: auto 100%;
  background-position: center !important;
  background-size: cover !important;
  /*&:active {
     transform:scale(0.9);
  }*/
}
.sectionGallery .options .option.active .shadow {
  box-shadow: inset 0 -120px 120px -120px black, inset 0 -120px 120px -100px black;
}
.sectionGallery .options .option.active .label {
  bottom: 20px;
  left: 20px;
}
.sectionGallery .options .option.active .label .info > div {
  left: 0px;
  opacity: 1;
}
.sectionGallery .options .option:not(.active) {
  flex-grow: 1;
  border-radius: 30px;
}
.sectionGallery .options .option:not(.active) .shadow {
  bottom: -40px;
  box-shadow: inset 0 -120px 0px -120px black, inset 0 -120px 0px -100px black;
}
.sectionGallery .options .option:not(.active) .label {
  bottom: 10px;
  left: 10px;
}
