}

.gallery_img::after {
	content: '';
	position: absolute;
	top: 50%;
	left: 50%;
	width: 200%;
	height: 200%;
	background: rgba(0, 0, 0, 0.5);
	-webkit-transform-origin: center;
	        transform-origin: center;
	opacity: 0;
	-webkit-transform: scale(2);
	        transform: scale(2);
	transition: opacity 300ms;
}

.gallery_img__long{
  object-fit: cover;
  height: 90%;
}
@media screen and (max-width: 768px) {
  .gallery_img {
    margin: 1rem auto;
  }
}

