}


/*Tooltip*/
figure {
	margin: 0;
	position: relative;
	overflow: hidden;
}

figure::after {
	content: '';
	position: absolute;
	top: 50%;
	left: 50%;
	width: 200%;
	height: 200%;
	background: rgba(0, 0, 0, 0.5);
	-webkit-transform-origin: center;
	        transform-origin: center;
	opacity: 0;
	-webkit-transform: scale(2);
	        transform: scale(2);
	transition: opacity 300ms;
}

figcaption {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	place-items: center;
	text-align: center;
	padding: 1rem;
	color: white;
	font-weight: 600;
	letter-spacing: 0.5rem;
	font-size: 1.2rem;
	z-index: 1;
	opacity: 0;
	transition: opacity 600ms, -webkit-transform 600ms;
	transition: opacity 600ms, transform 600ms;
	transition: opacity 600ms, transform 600ms, -webkit-transform 600ms;
}

.a:is(:hover, :focus) figure::after {
	opacity: 1;
}

.a:is(:hover, :focus) figcaption {
	opacity: 1;
	transition: opacity 600ms;
}

@media (prefers-reduced-motion: no-preference) {
	figcaption {
		-webkit-transform: translate3d(0, 2rem, 0);
		        transform: translate3d(0, 2rem, 0);
	}

	figure::after {
		border-radius: 50%;
		opacity: 1;
		-webkit-transform: scale(0);
		        transform: scale(0);
		transition: -webkit-transform 900ms;
		transition: transform 900ms;
		transition: transform 900ms, -webkit-transform 900ms;
	}

	.a:is(:hover, :focus) figure::after {
		-webkit-transform: scale(2.5);
		        transform: scale(2.5);
	}

	.a:is(:hover, :focus) figcaption {
		opacity: 1;
		-webkit-transform: translate3d(0, 0, 0);
		        transform: translate3d(0, 0, 0);
		transition: opacity 600ms 400ms, -webkit-transform 600ms 400ms;
		transition: opacity 600ms 400ms, transform 600ms 400ms;
		transition: opacity 600ms 400ms, transform 600ms 400ms, -webkit-transform 600ms 400ms;
	}

	.a:is(:hover, :focus) img {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}
}
